import * as React from "react";
export type GalleryImage = { src: string; alt: string };
const GALLERY_IMAGES: GalleryImage[] = [
  { src: "https://images.unsplash.com/photo-1532526674046-5b3f6d7d2ab1?w=500&auto=format&fit=crop&q=60&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxzZWFyY2h8N3x8VGV4dHVyZWQlMjBsb29wJTIwY2FycGV0JTIwb24lMjBzdGFpcmNhc2UlMjB3aXRoJTIwY2xlYW4lMjBub3Npbmd8ZW58MHx8MHx8fDA%3D", alt: "Textured loop carpet on staircase with clean nosing" },
  { src: "https://plus.unsplash.com/premium_photo-1722170080353-5b68cd7b16ff?w=500&auto=format&fit=crop&q=60&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MjF8fFBsdXNoJTIwY2FycGV0JTIwaW4lMjBwcmltYXJ5JTIwYmVkcm9vbSUyMHdpdGglMjBuZXV0cmFsJTIwdG9uZXN8ZW58MHx8MHx8fDA%3D", alt: "Plush carpet in primary bedroom with neutral tones" },
  { src: "https://images.unsplash.com/photo-1534889156217-d643df14f14a?w=500&auto=format&fit=crop&q=60&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxzZWFyY2h8Nnx8RmFtaWx5JTIwcm9vbSUyMHdpdGglMjBwYXR0ZXJuZWQlMjBjdXQlMjBwaWxlJTIwY2FycGV0fGVufDB8fDB8fHww", alt: "Family room with patterned cut-pile carpet" },
  { src: "https://images.unsplash.com/photo-1582574736964-02de9666c77f?w=500&auto=format&fit=crop&q=60&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MTR8fEhhbGx3YXklMjBydW5uZXIlMjBpbiUyMGR1cmFibGUlMjBiZXJiZXIlMjBsb29wJTIwY2FycGV0fGVufDB8fDB8fHww", alt: "Hallway runner in durable berber loop" },
  { src: "https://images.unsplash.com/photo-1698047806019-6eeae680537d?w=500&auto=format&fit=crop&q=60&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MTF8fExpdmluZyUyMHJvb20lMjBjYXJwZXQlMjB3aXRoJTIwc3VidGxlJTIwdGV4dHVyZSUyMGFuZCUyMHRpZ2h0JTIwc2VhbXN8ZW58MHx8MHx8fDA%3D", alt: "Living room carpet with subtle texture and tight seams" },
  { src: "https://images.unsplash.com/photo-1646215993316-c98f642303ce?w=500&auto=format&fit=crop&q=60&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxzZWFyY2h8OHx8b2ZmaWNlJTIwd2l0aCUyMGNhcnBldHxlbnwwfHwwfHx8MA%3D%3D", alt: "Home office with stain-resistant carpet" },
//   { src: "/images/carpet-gallery/07.jpg", alt: "Kid-friendly carpet in playroom with easy-clean fibers" },
//   { src: "/images/carpet-gallery/08.jpg", alt: "Staircase with tailored pattern match and crisp edges" },
];

 const CarpetGallerySection: React.FC<{ images?: GalleryImage[]; city?: string }> = ({
  images = GALLERY_IMAGES,
  city = "Boise",
}) => {
  const [isNarrow, setIsNarrow] = React.useState(false);
  const [active, setActive] = React.useState<number | null>(null);

  React.useEffect(() => {
    const onResize = () => setIsNarrow(window.innerWidth <= 980);
    onResize();
    window.addEventListener("resize", onResize);
    return () => window.removeEventListener("resize", onResize);
  }, []);

  React.useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (!active && active !== 0) return;
      if (e.key === "Escape") setActive(null);
      if (e.key === "ArrowRight") setActive((i) => (i === null ? 0 : (i + 1) % images.length));
      if (e.key === "ArrowLeft") setActive((i) => (i === null ? 0 : (i - 1 + images.length) % images.length));
    };
    document.addEventListener("keydown", onKey);
    return () => document.removeEventListener("keydown", onKey);
  }, [active, images.length]);

  const styles = {
    section: {
      background: "#f7f9fb",
      padding: isNarrow ? "40px 16px" : "64px 24px",
    } as React.CSSProperties,
    wrap: {
      maxWidth: 1200,
      margin: "0 auto",
    },
    headerRow: {
      display: "flex",
      alignItems: isNarrow ? "flex-start" : "center",
      justifyContent: "space-between",
      gap: 16,
      marginBottom: 18,
      flexDirection: isNarrow ? "column" as const : "row" as const,
    },
    eyebrow: {
      letterSpacing: ".08em",
      textTransform: "uppercase" as const,
      fontSize: 12,
      color: "#8E9595",
      fontWeight: 700,
      marginBottom: 6,
    },
    title: {
      margin: 0,
      fontSize: "clamp(22px, 3.6vw, 32px)",
      color: "#36434A",
      lineHeight: 1.2,
      fontWeight: 800,
    },
    subtitle: {
      margin: "6px 0 0",
      color: "#5a6a72",
      fontSize: isNarrow ? 14 : 15,
      maxWidth: 720,
    },
    cta: {
      display: "inline-block",
      background: "#36434A",
      color: "#f8f9fa",
      padding: "10px 16px",
      borderRadius: 999,
      textDecoration: "none",
      fontWeight: 700,
      boxShadow: "0 8px 20px rgba(0,0,0,0.12)",
      whiteSpace: "nowrap" as const,
    },

    grid: {
      display: "grid",
      gap: isNarrow ? 10 : 16,
      gridTemplateColumns: isNarrow ? "repeat(2, 1fr)" : "repeat(4, 1fr)",
    } as React.CSSProperties,


    itemBtn: {
      position: "relative" as const,
      display: "block",
      padding: 0,
      margin: 0,
      border: 0,
      background: "transparent",
      cursor: "pointer",
      borderRadius: 12,
      overflow: "hidden",
      boxShadow: "0 8px 22px rgba(0,0,0,0.12)",
      transition: "transform .2s ease, box-shadow .2s ease",
    },

    img: {
      width: "100%",
      height: 220,
      objectFit: "cover" as const,
      display: "block",
      transform: "scale(1)",
      transition: "transform .4s cubic-bezier(0.25,0.8,0.25,1)",
    },

    chip: {
      position: "absolute" as const,
      left: 12,
      top: 12,
      background: "rgba(54,67,74,0.9)",
      color: "#f8f9fa",
      padding: "6px 10px",
      fontSize: 12,
      borderRadius: 999,
      border: "1px solid rgba(255,255,255,0.25)",
      pointerEvents: "none" as const,
    },

    // lightbox
    lightbox: {
      position: "fixed" as const,
      inset: 0,
      background: "rgba(0,0,0,0.85)",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      padding: isNarrow ? 12 : 24,
      zIndex: 9999,
    },
    lightboxImg: {
      maxWidth: "100%",
      maxHeight: "84vh",
      borderRadius: 12,
      boxShadow: "0 20px 60px rgba(0,0,0,0.4)",
    },
    close: {
      position: "absolute" as const,
      top: 16,
      right: 16,
      background: "rgba(255,255,255,0.14)",
      color: "#f8f9fa",
      border: "1px solid rgba(255,255,255,0.35)",
      borderRadius: 999,
      padding: "8px 12px",
      fontWeight: 700,
      cursor: "pointer",
    },
    arrows: {
      position: "absolute" as const,
      bottom: 16,
      display: "flex",
      gap: 8,
    },
    arrowBtn: {
      background: "rgba(255,255,255,0.14)",
      color: "#f8f9fa",
      border: "1px solid rgba(255,255,255,0.35)",
      borderRadius: 999,
      padding: "8px 12px",
      fontWeight: 700,
      cursor: "pointer",
    },
  };

  return (
    <section style={styles.section} aria-labelledby="gallery-title">
      <div style={styles.wrap}>
        <div style={styles.headerRow}>
          <div>
            <div style={styles.eyebrow}>Our Work</div>
            <h2 id="gallery-title" style={styles.title}>
              Recent Carpet Installs in {city}
            </h2>
            <p style={styles.subtitle}>
              A quick look at plush, loop, and patterned installations—precision seams, crisp stair
              nosing, and clean finishes our clients love.
            </p>
          </div>
          
        </div>

        <div style={styles.grid}>
          {images.map((img, i) => (
            <button
              key={i}
              style={styles.itemBtn}
              onClick={() => setActive(i)}
              aria-label={`Open larger view: ${img.alt}`}
              onMouseEnter={(e) => {
                const el = e.currentTarget.querySelector("img") as HTMLImageElement | null;
                if (el) el.style.transform = "scale(1.04)";
              }}
              onMouseLeave={(e) => {
                const el = e.currentTarget.querySelector("img") as HTMLImageElement | null;
                if (el) el.style.transform = "scale(1)";
              }}
            >
              <img src={img.src} alt={img.alt} style={styles.img} />
              <span style={styles.chip}>Carpet</span>
            </button>
          ))}
        </div>
      </div>

      {/* Lightbox */}
      {(active || active === 0) && (
        <div role="dialog" aria-modal="true" style={styles.lightbox} onClick={() => setActive(null)}>
          <img
            src={images[active].src}
            alt={images[active].alt}
            style={styles.lightboxImg}
            onClick={(e) => e.stopPropagation()}
          />
          <button aria-label="Close gallery" style={styles.close} onClick={() => setActive(null)}>
            ✕ Close
          </button>
          <div style={{ ...styles.arrows, left: "50%", transform: "translateX(-50%)" }}>
            <button
              aria-label="Previous image"
              style={styles.arrowBtn}
              onClick={(e) => {
                e.stopPropagation();
                setActive((i) => (i === null ? 0 : (i - 1 + images.length) % images.length));
              }}
            >
              ← Prev
            </button>
            <button
              aria-label="Next image"
              style={styles.arrowBtn}
              onClick={(e) => {
                e.stopPropagation();
                setActive((i) => (i === null ? 0 : (i + 1) % images.length));
              }}
            >
              Next →
            </button>
          </div>
        </div>
      )}
    </section>
  );
};
export default CarpetGallerySection;
